import * as XLSX from 'xlsx';
import { saveAs } from 'file-saver';
import { KeywordData, TitleSubtitleData, ColumnInfo } from '../types';

export class ExportUtils {
  /**
   * Debug: Veri formatn kontrol et
   */
  static debugDataFormat(data: KeywordData[] | TitleSubtitleData[], columnInfo?: ColumnInfo[]): void {
    if (data.length === 0) {
      console.log('Debug: Veri bo');
      return;
    }
    
    const firstRow = data[0];
    console.log('Debug: lk satr veri tipleri:');
    
    Object.entries(firstRow).forEach(([key, value]) => {
      const isNumeric = this.isNumericColumn(key, columnInfo);
      console.log(`${key}: ${typeof value} = ${value} ${isNumeric ? '(saysal stun)' : ''}`);
    });
    
    // Saysal stunlarn rnek deerlerini kontrol et
    const numericColumns = Object.keys(firstRow).filter(key => this.isNumericColumn(key, columnInfo));
    console.log('Debug: Saysal stunlar:', numericColumns);
    
    numericColumns.forEach(column => {
      const values = data.slice(0, 5).map(row => row[column as keyof typeof row]);
      console.log(`${column} rnek deerleri:`, values);
    });
  }
  
  /**
   * Excel dosyas olutur ve indir
   */
  static exportToExcel(
    data: KeywordData[] | TitleSubtitleData[],
    filename: string,
    sheetName: string = 'ASO Data',
    columnInfo?: ColumnInfo[]
  ): void {
    try {
      // Veriyi hazrla - saysal deerleri doru formatta tut
      const processedData = data.map(row => {
        const processedRow: any = {};
        
        Object.entries(row).forEach(([key, value]) => {
          // Saysal stunlar iin zel ilem
          if (this.isNumericColumn(key, columnInfo)) {
            const numericValue = this.ensureNumericValue(value);
            // Saysal deeri kesinlikle number olarak tut
            processedRow[key] = numericValue;
          } else {
            processedRow[key] = value;
          }
        });
        
        return processedRow;
      });
      
      // Headers' al
      const headers = Object.keys(processedData[0] || {});
      
      // 2D array olutur (header + data)
      const worksheetData = [
        headers, // lk satr header
        ...processedData.map(row => 
          headers.map(header => {
            const value = row[header];
            // Saysal stunlar iin number olarak tut
            if (this.isNumericColumn(header, columnInfo)) {
              return this.ensureNumericValue(value);
            }
            return value;
          })
        )
      ];
      
      // Worksheet olutur
      const ws = XLSX.utils.aoa_to_sheet(worksheetData);
      
      // Saysal stunlar iin format ayarlar
      this.applyNumericFormats(ws, processedData, columnInfo);
      
      // Workbook olutur
      const wb = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, ws, sheetName);
      
      // Excel dosyasn buffer olarak olutur
      const excelBuffer = XLSX.write(wb, { 
        bookType: 'xlsx', 
        type: 'array',
        cellStyles: true,
        compression: true
      });
      const blob = new Blob([excelBuffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
      
      // Dosyay indir
      const timestamp = new Date().toISOString().replace(/[:.]/g, '-').slice(0, 19);
      const fullFilename = `${filename}_${timestamp}.xlsx`;
      saveAs(blob, fullFilename);
      
    } catch (error) {
      console.error('Excel export error:', error);
      throw new Error('Excel dosyas oluturulamad');
    }
  }
  
  /**
   * Saysal stun olup olmadn kontrol et
   */
  private static isNumericColumn(columnName: string, columnInfo?: ColumnInfo[]): boolean {
    // Dinamik stun kontrol
    if (columnInfo) {
      const columnData = columnInfo.find(col => col.name === columnName);
      if (columnData) {
        return columnData.type === 'number' || columnData.type === 'percentage';
      }
    }
    
    // Geriye uyumluluk iin eski kontrol
    const numericColumns = [
      'Volume', 
      'Difficulty', 
      'Growth (Max Reach)', 
      'Max. Reach', 
      'No. of results',
      'Title_Length',
      'Subtitle_Length', 
      'Keywords_Length',
      'Total_Volume',
      'Total_Difficulty',
      'Average_Volume',
      'Average_Difficulty',
      'Matched_Keywords_Count'
    ];
    return numericColumns.includes(columnName);
  }
  
  /**
   * Deeri saysal formatta tut
   */
  private static ensureNumericValue(value: any): number {
    if (value === null || value === undefined || value === '') {
      return 0;
    }
    
    try {
      if (typeof value === 'number') {
        return isNaN(value) ? 0 : value;
      }
      
      // String ise temizle ve parse et
      if (typeof value === 'string') {
        const cleaned = value.replace(/,/g, '').replace(/%/g, '').replace(/\s/g, '').trim();
        if (cleaned === '' || cleaned === '-') {
          return 0;
        }
        const parsed = parseFloat(cleaned);
        return isNaN(parsed) ? 0 : parsed;
      }
      
      // Dier tipler iin string'e evir ve parse et
      const stringValue = String(value);
      const cleaned = stringValue.replace(/,/g, '').replace(/%/g, '').replace(/\s/g, '').trim();
      const parsed = parseFloat(cleaned);
      return isNaN(parsed) ? 0 : parsed;
    } catch {
      return 0;
    }
  }
  
  /**
   * Saysal stunlar iin format ayarlar uygula
   */
  private static applyNumericFormats(ws: XLSX.WorkSheet, data: any[], columnInfo?: ColumnInfo[]): void {
    if (data.length === 0) return;
    
    const headers = Object.keys(data[0]);
    const numericColumns: { [key: string]: number } = {};
    
    // Saysal stunlarn indekslerini bul
    headers.forEach((header, index) => {
      if (this.isNumericColumn(header, columnInfo)) {
        numericColumns[header] = index;
      }
    });
    
    // Her saysal stun iin format ayarla
    Object.entries(numericColumns).forEach(([columnName, colIndex]) => {
      const colLetter = XLSX.utils.encode_col(colIndex);
      
      // Stun genilii ayarla
      if (!ws['!cols']) ws['!cols'] = [];
      ws['!cols'][colIndex] = { width: 15 };
      
      // Her hcre iin saysal format uygula (header'dan sonraki satrlar)
      data.forEach((row, rowIndex) => {
        const cellAddress = `${colLetter}${rowIndex + 2}`; // +2 nk header var ve Excel 1'den balar
        const cellValue = row[columnName];
        
        // Saysal deeri kesinlikle number olarak ayarla
        const numericValue = this.ensureNumericValue(cellValue);
        
        // Hcreyi olutur veya gncelle
        ws[cellAddress] = {
          v: numericValue, // value
          t: 'n', // type: number
          z: '#,##0' // format: number with thousands separator
        };
      });
    });
  }
  
  /**
   * CSV dosyas olutur ve indir
   */
  static exportToCsv(
    data: KeywordData[] | TitleSubtitleData[],
    filename: string,
    columnInfo?: ColumnInfo[]
  ): void {
    try {
      if (data.length === 0) {
        throw new Error('Da aktarlacak veri yok');
      }
      
      // CSV balklarn olutur
      const headers = Object.keys(data[0]);
      const csvContent = [
        headers.join(','),
        ...data.map(row => 
          headers.map(header => {
            const value = row[header as keyof typeof row];
            // Saysal deerleri doru formatta tut
            if (this.isNumericColumn(header, columnInfo)) {
              const numericValue = this.ensureNumericValue(value);
              return numericValue.toString();
            }
            // Virgl ieren deerleri trnak iine al
            const stringValue = String(value || '');
            if (stringValue.includes(',')) {
              return `"${stringValue}"`;
            }
            return stringValue;
          }).join(',')
        )
      ].join('\n');
      
      // Blob olutur
      const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
      
      // Dosyay indir
      const timestamp = new Date().toISOString().replace(/[:.]/g, '-').slice(0, 19);
      const fullFilename = `${filename}_${timestamp}.csv`;
      saveAs(blob, fullFilename);
      
    } catch (error) {
      console.error('CSV export error:', error);
      throw new Error('CSV dosyas oluturulamad');
    }
  }
  
  /**
   * Dosya adn gvenli hale getir
   */
  static sanitizeFilename(filename: string): string {
    return filename
      .replace(/[<>:"/\\|?*]/g, '_')
      .replace(/\s+/g, '_')
      .toLowerCase();
  }
  
  /**
   * Timestamp olutur
   */
  static generateTimestamp(): string {
    return new Date().toISOString().replace(/[:.]/g, '-').slice(0, 19);
  }
} 